/***** ABOUT ******/

#about {
    width:100%;
    padding:70px 50px;
    margin: 0;
    background: #dddddd;
    display: flex;
    justify-content: center;
    align-items: stretch;
    
    @mixin atMobile {
    padding:40px 15px;
    flex-direction: column;
    align-items: center;
    }
}

#about div {
    width: 50%; 
    padding:50px;
    
    @mixin atMobile {
    width: 100%; 
    padding:0px;
    }
}

#about div img {
    max-width: 100%;
    outline: thin dotted #000;
}

