html, 
body {
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    letter-spacing: 2px;
    line-height: 2em;
    font-family: 'Roboto', sans-serif;
}

* {
    box-sizing: border-box;
}

img {
    max-width: 100%;
    margin: 0;
}

ul, 
menu, 
dir {
    list-style-type: none !important;
    margin: 0;
}

.text-accent-color {
    color: #b31e24;
}

.bg-accent-color {
    background: #b31e24;
}