#blog {
    width:100%;
    margin:0;
    padding:70px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    background:#dddddd;
    
    @mixin atMobile {
    flex-direction: column !important;
    align-items: center;
    padding:15px;
    }
}

.blog-card {
    flex: 1;
    background:#ffffff;
    border:thin dotted black;
    margin: 10px;
    max-width: 400px;
    min-width: 350px;
    
    @mixin atMobile {
    margin-left:0;
    margin-bottom:20px;
    max-width: 100%;
    min-width: 100%;   
    }
}

.blog-card header {
    padding: 10px;
    color: #fff;
}

.blog-card header h2 {
   font-size: 1.4rem;
   font-weight: normal;
  margin: 0;
  padding: 0;
}

.blog-card-imgdiv {
    width:100%;
    height:200px;
    overflow: hidden;
    background:none;
    margin:0;
    padding: 0;
    
}

.blog-card img {
    width: 100%;
    height: auto;
}

.blog-card-content {
  padding: 5px 20px 30px 20px;
  font-size: .9rem;
  color: #757575;
  word-wrap: break-word; 
}

