/* @imports */
@import "normalize.css";

@import "base/_global";
@import "base/_mixins";
@import "modules/_header";
@import "modules/_hero";
@import "modules/_about";
@import "modules/_address";
@import "modules/_image-post";
@import "modules/_btn";
@import "modules/_blog-section";
@import "modules/_footer";

/* Variables */
$mainAccent: green;


/***** OUR STYLE ***/
#our-style {
   width:100%;
    padding:70px;
    margin: 0;
    background:#fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    
    @mixin atMobile {
    padding: 40px 9px;   
    }
}

#our-style div p {
    text-align: left;
    max-width: 700px;
}

#map {
    width:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background:#000000;
    border-top:#000000 1px solid;
    border-bottom:#000000 1px solid;
    padding:0;
    margin:0;
}

#parks-recs {
    margin: 0;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

/*****  blog page ****/

#blog-page {
    width:100%;
    margin:0;
    padding:70px 100px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    background:#dddddd;
    
    @mixin atMobile d{
    flex-direction: column !important;
    align-items: center;
    padding:30px;   
    }
}

/****** page-content *****/

.page-content {
    width:100%;
    margin:0;
    padding:70px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background:#ffffff;
}
  



    
